.popup__container {
  position: relative;
  background: #fff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  width: 430px;
  box-sizing: border-box;
  max-height: 330px;
}


@media screen and (max-width: 450px) {
  .popup__container {
    max-width: 282px;
  }
}
